import { Component, h, Prop } from '@stencil/core';
import { recipes, RecipesData } from "../../../global/data/recipes";

@Component({
    tag: 'ks-page-recipes',
    styleUrl: 'recipes.css'
})
export class PageRecipes{
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() recipesData: string;

    componentWillLoad() {
        const recipesDataElement = document.getElementById(this.recipesData);
		const recipesData = JSON.parse(recipesDataElement.innerHTML) as RecipesData;

        Object.keys(recipesData).map(key => {
			recipes.set(key as keyof RecipesData, recipesData[key]);
		});
    }

	render() {
        const navigation = recipes.get('navigation');
        const cards = recipes.get('recipes');

		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>

            <ks-listing-header
                heading={recipes.get('title')}
                breadcrumbs={recipes.get('breadcrumbs')}
                description={recipes.get('description')}
                categories={recipes.get('categories')}
                tags={recipes.get('tags')}
            />

            {navigation && cards?.length > 0 ?
                <ks-listing-navigation>
                    <ks-pagination count={navigation.count} current={navigation.current} base={navigation.paginationBase} pattern={navigation.pattern} />
                </ks-listing-navigation>
            : null}

            {cards?.length > 0 ?
                <ks-article-container>
                    {cards.map(card => 
                        <ks-recipe-card
                            heading={card.heading} link={card.link}
                            cuisine={card.cuisine} cuisine-link={card.cuisineLink}
                            category={card.category} category-link={card.categoryLink}
                            image={card.image} width={card.width} height={card.height}
                        />
                    )}
                </ks-article-container>
            : null}

            {navigation && cards?.length > 0 ?
                <ks-listing-navigation>
                    <ks-pagination count={navigation.count} current={navigation.current} base={navigation.paginationBase} pattern={navigation.pattern} />
                </ks-listing-navigation>
            : null}

        </ks-page-base>;
	}
}
