import { createStore } from "@stencil/store";
import { NameLinkData, ListingNavigationData } from "./listing";

export const recipes = createStore({} as RecipesData);

export interface RecipesData {
    title: string;
    description: string;
    bottomDescription: string;

    breadcrumbs: NameLinkData[];
    categories: NameLinkData[];
    tags: NameLinkData[];
    recipes: RecipeCardData[];

    navigation: ListingNavigationData;
}

export interface RecipeCardData {
    heading: string;
    link: string;
    cuisine: string;
    cuisineLink: string;
    category: string;
    categoryLink: string;
    image: string;
    webp: string;
    width: number;
    height: number;
}